/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --main-bg-color: #E9ECEF;
  --main-bg-color-dark: #000;
  --main-font-color:#383a3b;
  --fawe-icons-color: #0C56B3;
  --odph-color: #ff0000;
  --text-white-color: #E9ECEF;
  
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";

}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

a {
  color: #000;
  text-decoration: none;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}


.nav-ulist {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    margin-top: 0;
    list-style: none;
}

.nav-link {
    font-size: 1.1rem;
    letter-spacing: 0.1rem;
    
}




@media screen and (min-width: 768px) {
    .nav-ulist {
        flex-direction: row;
        justify-content: space-around;     
    }

    .nav-link {
        margin: 0 2rem;
        font-size: 1.1rem;
    }
}


.nav-header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    height: auto;
    font-size: 1.5rem;
    padding: .5rem 1rem;
}

.nav-logo:before {
    content: '{ ';
}

.nav-logo {
    font-family: 'Montserrat', sans-serif;
}

.nav-logo:after {
    content: ' }';
}

.btn {
    /* the tricks */
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    border: none;
   
    color: rgb(121, 121, 121);
    background-color: transparent;
    /*transition: 2s ease-in-out;*/
}

.btn:active {
    -webkit-transform: scale(90%) rotate(90deg);
            transform: scale(90%) rotate(90deg);
}

.hamburger {
    line-height: 10px;
}

.nav-links {
    flex-basis: 100%;
    flex-grow: 1;
    overflow: hidden;
    align-items: center;
    height: 7rem;
    transition: height .5s ease-out;
}


.no-show {
    position: relative; 
    height: 0;
    transition: height .5s ease-out;
}

@media screen and (min-width: 768px) {
    
    .nav-header {
        flex-direction: row;
        justify-content: flex-start;
    }
    
    .nav-links {
        overflow: visible;
        flex-basis: 0;
        flex-grow: 0;
        height: 2rem;
    }
    
    .nav-btn {
        display: none;
    }
}
.nav-bar {
    background-color: rgba(255, 255, 255, .3);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, .37);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    height: -webkit-max-content;
    height: max-content;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10;
}
.hero {
    display: grid;
    place-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: color-burn;
    height: 30vh;
    min-height: 30vh;
    width: 100vw;
}

.hero-h1 {
    color: red;
    font-size: calc(1rem + 5vw);
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
}

.hero-h1:before {
    content: '{  ';
}

.hero-h1:after {
    content: '  }';
}

@media screen and (min-width: 768px) {

    .hero {
        height: 100vh;
    }

}
.main-info {
    background-color: var(--main-bg-color);
    display: grid;
    align-content: center;
    min-height: 30vh;
    height: 30vh;
    
}

.main-info-h1 {
    color: var(--main-font-color);
    font-family: 'Montserrat', sans-serif;
    font-size: clamp(2rem, 0.75rem + 1.5vw, 2.4rem);
    text-align: center;
    text-transform: capitalize;
    font-weight: 500;
}

.main-info-p {
    color: var(--main-font-color);
    font-family: 'Montserrat', sans-serif;
    font-size: clamp(1.5rem, 0.75rem + 1.5vw, 1.4rem);
    font-weight: 300;
    text-align: center;
}
.fe-tech {
    background-color: var(--main-bg-color-dark);
    text-align: center;
    height: 20vh;
}

.img-container {
    margin: 2rem auto;
}
.img-list {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    padding: 0;
} 

.img-list-li {
    font-size: 3rem;
    color: #fff;
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
    text-shadow: 0 0 1.5rem #0072ff, 0 0 2rem #0072ff, 0 0 2.5rem #0072ff, 0 0 3rem #0072ff;
}

.esp-react {
    -webkit-animation: rota-react 18s linear infinite;
            animation: rota-react 18s linear infinite;
}

.fa-react {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.fa-html5 {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.fa-css3 {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.fa-js {
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
}


.img-tech {
    width: 5rem;
    height: 5rem;
    object-fit: cover;
}

@-webkit-keyframes animate {
    from {
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
    to {
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

@keyframes animate {
    from {
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
    to {
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

@-webkit-keyframes rota-react {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

@keyframes rota-react {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

